body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (min-width: 768px) {
  body div.layout-component.drawer-open header, body div.layout-component.drawer-open .layout-content {
    margin-left: 320px;
    width: calc(100% - 320px); } }

.hidden {
  display: none !important; }

body {
  height: 100vh; }
  body div.layout-component .layout-content {
    padding: 10px; }
  body div.layout-component header, body div.layout-component .layout-content {
    transition: margin-left 225ms cubic-bezier(0, 0, 0.2, 1) 0ms, width 225ms cubic-bezier(0, 0, 0.2, 1) 0ms;
    width: 100%; }
  body div.layout-component header .btn-logout {
    position: absolute;
    right: 5px; }

.flex-hbox {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column; }

.flex-item {
  -webkit-flex: 1 1;
          flex: 1 1; }

.giant-progress-container-wrapper {
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center; }
  .giant-progress-container-wrapper .giant-progress-container {
    text-align: center; }

div.drawer-paper {
  width: 320px; }

.note-list-component .search-input-wrapper {
  display: -webkit-flex;
  display: flex;
  padding-right: 10px;
  padding-left: 10px; }
  .note-list-component .search-input-wrapper .search-input {
    -webkit-flex: 1 1;
            flex: 1 1; }
  .note-list-component .search-input-wrapper .search-icon {
    height: unset;
    margin-right: 8px; }

.note-list-component .search-progress-complete {
  visibility: hidden; }

.note-list-component .list-of-notes .add-note-btn {
  background-color: #673ab7; }

.note-list-component .list-of-notes .list-item-note-selected {
  background-color: #00e5ff; }
  .note-list-component .list-of-notes .list-item-note-selected .note-item-text-selected > span {
    color: rgba(0, 0, 0, 0.87);
    font-weight: 500; }

.note-editor-component .actions-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row-reverse;
          flex-direction: row-reverse; }

.note-editor-component .title-wraper {
  display: -webkit-flex;
  display: flex;
  padding: 10px; }
  .note-editor-component .title-wraper .editor-save-progress {
    width: 40px;
    height: 40px;
    position: absolute;
    right: 70px;
    pointer-events: none; }
  .note-editor-component .title-wraper .txt-title {
    -webkit-flex: 1 1;
            flex: 1 1; }

.note-editor-component .quill-paper-wrapper {
  margin-top: 10px; }
  .note-editor-component .quill-paper-wrapper .delete-btn {
    -webkit-transform: translate3d(0px, -51px, 0px);
            transform: translate3d(0px, -51px, 0px);
    right: 20px;
    position: absolute; }

.note-editor-component .quill {
  border-color: transparent; }
  .note-editor-component .quill .ql-snow.ql-toolbar {
    background-color: #00e5ff; }
    .note-editor-component .quill .ql-snow.ql-toolbar .ql-picker-options {
      background-color: #00e5ff; }
  .note-editor-component .quill .ql-snow.ql-container {
    border-color: transparent;
    color: white; }
    .note-editor-component .quill .ql-snow.ql-container .ql-editor {
      min-height: 250px; }
      .note-editor-component .quill .ql-snow.ql-container .ql-editor.ql-blank::before {
        color: gray; }

.ql-snow .ql-picker.ql-expanded .ql-picker-label .ql-stroke {
  stroke: #4c4646 !important; }

.ql-snow .ql-picker.ql-expanded .ql-picker-label {
  color: #4c4646 !important; }

